import { CURRENCY_LIST } from "../util/currencies";
import { updateDonationText, isFormValid } from "../util/updateText";

FCBH.docReady(() => {
  FCBH.deleteFromStorage(FCBH.GIVE_FORM);
  initCampain();
});

function initCampain() {
  setProgram();
  setDefaultAmount();
  processAmountChange();
  processCurrencyList();
  processType();
  setPledgeMonths();
  processContinueBtn();
  processForm();

  function processForm(){
    const form = document.querySelector('form#wf-form-campaign-form')
    form.addEventListener('submit', (ev)=>{
      ev.preventDefault();
      ev.stopPropagation();
      ev.stopImmediatePropagation();
    })
  }

  function setProgram() {
    const project_fund_code =
      document.getElementById("project-fund-code")?.value || "VISION2033-Q";
    const project_title =
      document.getElementById("title")?.value || "Vision 2033";
    const project_id = document.getElementById("project-id")?.value || "3394";
    const outreach = document.getElementById("outreach")?.value || "FCBH";
    const program = {
      project_fund_code,
      project_title,
      project_id,
      outreach,
      textContent: "",
      label: project_title,
    };
    saveFormInStorage("program", program);
  }

  function processCurrencyList() {
    const selectedCurrency = document.querySelector(
      "#select-currency #Currency div"
    );
    const currencyNav = document.querySelector(
      "#select-currency .dropdown-list"
    );
    currencyNav.style["max-height"] = "300px";
    currencyNav.style.overflow = "auto";
    while (currencyNav.lastChild) {
      currencyNav.removeChild(currencyNav.lastChild);
    }
    const nodes = CURRENCY_LIST.map((currency) => {
      const node = document.createElement("a");
      node.href = "#";
      node.classList.add("dropdown-link", "w-dropdown-link");
      node.textContent = currency.label;
      node.setAttribute("data-value", currency.value);
      const actionFn = () => {
        const currentLabel = selectedCurrency.textContent;
        const currentValue = selectedCurrency.getAttribute("data-value");
        selectedCurrency.textContent = currency.label;
        selectedCurrency.setAttribute("data-value", currency.value);
        saveFormInStorage("currency_code", currency.value);
        selectedCurrency.dispatchEvent(
          new MouseEvent("mouseup", {
            view: window,
            bubbles: true,
            cancelable: false,
          })
        );
        updateAmounts(currentValue, currency.value).catch(() => {
          selectedCurrency.textContent = currentLabel;
          selectedCurrency.setAttribute("data-value", currentValue);
          saveFormInStorage("currency_code", currentValue);
        });
      };
      node.addEventListener("click", actionFn);
      node.addEventListener("touchstart", actionFn);
      return node;
    });
    selectedCurrency.textContent = "USD - United States Dollar";
    selectedCurrency.setAttribute("data-value", "USD");
    saveFormInStorage("currency_code", "USD");
    currencyNav.append(...nodes);
  }

  function processType() {
    const nodes = document.querySelectorAll("#type-dropdown a");
    const selectedType = document.querySelector("#type-toggle div");
    for (const node of nodes) {
      const text = node.textContent.trim();
      const actionFn = () => {
        switch (text) {
          case "Give Once":
            saveFormInStorage("recurring_donation", "no");
            selectedType.textContent = "Give Once";
            break;
          case "Monthly":
            saveFormInStorage("recurring_donation", "5");
            selectedType.textContent = "Monthly";
            break;
          case "Pledge":
            saveFormInStorage("recurring_donation", "Pledge");
            selectedType.textContent = "Pledge";
            break;
        }
      };
      node.addEventListener("click", actionFn);
      node.addEventListener("touchstart", actionFn);
    }
  }

  function setPledgeMonths() {
    const nodes = document.querySelectorAll("#pledge-dropdown a");
    const selectedPledge = document.querySelector("#pledge-toggle div");

    for (const node of nodes) {
      const text = node.textContent.trim();

      const actionFn = () => {
        switch (text) {
          case "6 months":
            saveFormInStorage("pledge_time", "6");
            selectedPledge.textContent = "6 months";
            break;
          case "12 months":
            saveFormInStorage("pledge_time", "12");
            selectedPledge.textContent = "12 months";
            break;
        }
      };
      node.addEventListener("click", actionFn);
      node.addEventListener("touchstart", actionFn);
    }
    nodes[0].dispatchEvent(new Event("click"));
  }

  function setDefaultAmount() {
    const input = document.getElementById("other-quantity-2");
    input.value = "0";
    input.dispatchEvent(new Event("change"));
    saveFormInStorage("amounts", { value: input.value, usdValue: input.value });
  }

  function processAmountChange() {
    const input = document.getElementById("other-quantity-2");
    input.style.paddingLeft = "25px";
    const sufix = document.createElement("span");
    sufix.id = "other-qty-currency";
    sufix.textContent = "$";
    sufix.classList.add("currency-sufix");
    input.parentElement.append(sufix);
    input.addEventListener("change", async (ev) => {
      const value = ev.target.value;
      const currentCurency = document
        .querySelector("#select-currency #Currency div")
        .getAttribute("data-value");
      let usdValue = value;
      if (currentCurency !== "USD") {
        usdValue = await convertCurrency(currentCurency, "USD", value);
      }
      saveFormInStorage("amounts", {
        value,
        usdValue,
      });
    });
  }

  async function updateAmounts(fromCurrency, toCurrency) {
    if (fromCurrency === toCurrency) {
      return;
    }

    const currentForm = FCBH.getFromStorage(FCBH.GIVE_FORM) || {};
    const usdValue = await convertCurrency(
      toCurrency,
      "USD",
      currentForm.amounts.value
    );
    saveFormInStorage("amounts", {
      value: currentForm.amounts.value,
      usdValue,
    });
  }

  /**
   * Get's converted amount from server
   * @param {String} fromCurrency
   * @param {String} toCurrency
   * @param {String} fromAmount
   */
  async function convertCurrency(fromCurrency, toCurrency, fromAmount) {
    const currency_convert_post_url = `${FCBH.BASE_URL}ci/currency_convert/convert`;

    fromAmount = String(fromAmount);
    fromAmount = fromAmount.replace("$", "");
    fromAmount = parseFloat(fromAmount.replace(",", ""));

    const url =
      currency_convert_post_url +
      "/" +
      fromCurrency +
      "/" +
      toCurrency +
      "/" +
      fromAmount;

    const result = await FCBH.axiosInstance.post(url);

    return result.data;
  }

  function disableWhenInvalid() {
    document.getElementById("donation-text").style.display = "none";
    document.querySelector("#give-2 a").disabled = true;
  }

  function enableWhenValid() {
    document.getElementById("donation-text").style.display = "block";
    document.querySelector("#give-2 a").disabled = false;
  }

  function processContinueBtn() {
    const continueBtn = document.querySelector("#give-2 a");
    const linkTo = continueBtn.href;
    continueBtn.href = "#";
    continueBtn.role = "button";
    continueBtn.addEventListener("click", () => {
      if (continueBtn.disabled) {
        return;
      }
      const currentForm = FCBH.getFromStorage(FCBH.GIVE_FORM) || {};
      if (currentForm.recurring_donation === "Pledge") {
        window.location = "/vision-virtual-summit-2020-6-or-12-months";
      } else {
        window.location = linkTo;
      }
    });
  }

  function saveFormInStorage(key, value) {
    const currentForm = FCBH.getFromStorage(FCBH.GIVE_FORM) || {};
    const newForm = { ...currentForm, [key]: value };
    FCBH.saveToStorage(FCBH.GIVE_FORM, newForm);
    if (!isFormValid(newForm)) {
      disableWhenInvalid();
    } else {
      enableWhenValid();
      updateDonationText(newForm);
    }
  }
}
