import last from 'lodash/last';

export function capitalize(sentence) {
    return sentence.charAt(0).toUpperCase() + sentence.slice(1).toLowerCase()
}

export function formatDonationText(amount) {
    const amountWithCommasRegex = /(\d)(?=(\d\d\d)+(?!\d))/g
    const amountParsed = (amount % 1) !== 0 ? amount.toFixed(2).replace(amountWithCommasRegex, "$1,") : amount.toString().replace(amountWithCommasRegex, "$1,")
    return amountParsed
}

export const getFormCampaign = ( objName ) => {
    const campaign = last(window.location.href.split('/'));

    return { 
        campaign,
        localForm: `${objName}/${campaign}`
    };
};