import { formatDonationText } from "../util/helpers"

export function isFormValid({ currency_code, amounts, recurring_donation, program }){
  if (!currency_code || !amounts || !recurring_donation || !program) {
    return false;
  }
  return true
}

export function updateDonationText({ currency_code, amounts, recurring_donation, program }) {
  if (!isFormValid({ currency_code, amounts, recurring_donation, program })) {
    return;
  }
  const amountWithoutComma = amounts.value.toString().replace(',', '')
  const amount = parseFloat(amountWithoutComma)
  const donationText = document.querySelector("#wf-form-campaign-form > div.full-col__input-wrapper.txt-center > p");
  const amountParsed = formatDonationText(amount)
  const newText = `You’re ${recurring_donation === "Pledge" ? "pledging" : "donating"} ${currency_code === "USD" ? "$" : `${currency_code} `}${
    Number.isNaN(parseFloat(amountParsed)) ? 0 : amountParsed
  } ${
    recurring_donation === "no" ? "one time" : recurring_donation === "Pledge" ? "" : "each month"
  } for ${program.label}. `;
  donationText.dispatchEvent(
    new MouseEvent("mouseup", {
      view: window,
      bubbles: true,
      cancelable: false,
    })
  );
  donationText.dispatchEvent(
    new MouseEvent("click", {
      view: window,
      bubbles: true,
      cancelable: false,
    })
  );
  donationText.textContent = newText;
}